.conga-viewer {
    font-family: $font-family;
    background: transparent;
    * {
        box-shadow: none !important;
    }
    select::-ms-expand {
        display: none;
    }
    .conga {
        &-top-toolbar {
            &__centered-content {
                position: absolute;
                left: 50%;
                top: 6px;
                transform: translateX(-50%);
            }
            .dropdownToolbarButton {
                position: relative;
                top: 9px;
                background: transparent;
            }
        }
    }
    #viewer {
        .page {
            border-image: none;
            background: transparent;
            border: none;
            margin: $spacing-large auto;
        }
    }
    #thumbnailView {
        width: 200px;
    }
    .thumbnail {
        >:first-child {
            height: auto !important;
        }
    }
    #mainContainer {
        background: $color-background-temp-modal;
    }
    #toolbarContainer {
        background: $color-background-inverse;
        height: auto;
        position: absolute;
        top: 0;
        color: white;
    }
    #toolbarSidebar {
        background: #061c3f;
        height: auto;
        .splitToolbarButton {
            margin: 6px 2px 1px 7px;
        }
    }
    #sidebarContent {
        background: $color-background-alt-inverse;
        top: 36px;
    }
    #viewerContainer {
        top: 36px;
    }
    .dropdownToolbarButton {
        margin: 0;
        width: auto;
        max-width: none;
        > select {
            min-width: 0;
            -moz-appearance: none;
            -webkit-appearance: none;
            padding-left: 6px;
            border-radius: 1px;
        }
    }
    #toolbarViewer {
        position: relative;
        margin: 2px 5px;
        border-top: 1px solid rgba(0, 0, 0, .32);
        box-shadow: inset 0 1px 0 hsla(0, 0%, 100%, .08);
        .dropdownToolbarButton {
            margin-top: 6px;
            max-width: none !important;
        }
    }
    .findbar,
    .secondaryToolbar {
        top: 46px;
        background: $color-background-inverse;
    }
    html[dir=ltr] {
        .findbar {
            left: 58px;
        }
    }
    .doorHanger:after,
    .doorHanger:before,
    .doorHangerRight:after,
    .doorHangerRight:before,
    .doorHanger:after,
    .doorHangerRight:after {
        border-bottom-color: $color-background-inverse;
    }
    #overlayContainer {
        > .container {
            > .dialog {
                background: #061c3f;
            }
        }
    }
    #viewBookmark, #secondaryViewBookmark {
        display: none;
    }
    #openFile, #secondaryOpenFile {
        display: none;
    }
    #documentPropertiesClose {
        background-color: rgba(255, 255, 255, 0.09);
        background-image: -webkit-linear-gradient(rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0));
        background-image: linear-gradient(rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0));
        padding: 4px 8px;
        &:hover {
            text-decoration: none;
            background-color: rgba(255, 255, 255, 0.11);
            border-color: rgba(0, 0, 0, 0.4) rgba(0, 0, 0, 0.43) rgba(0, 0, 0, 0.45);
        }
    }
}
