html, body, .noSidebarCell, .sidebarCell .fixed {
    margin: 0 !important;
    padding: 0 !important;
    background: white;
}

html {
    background: transparent;
}

body.conga-transparent, body.conga-viewer {
    background: transparent !important;
}

/* ----------------------------------------
SLDS Design Tokens
---------------------------------------- */
@import 'slds-tokens/bg-actions',
    'slds-tokens/bg-custom',
    'slds-tokens/bg-icons',
    'slds-tokens/bg-standard',
    'slds-tokens/force-base',
    'slds-tokens/force-mq-commons';

/* ----------------------------------------
Partials
---------------------------------------- */
@import 'variables',
    'mixins';

/* ----------------------------------------
Project Styles
---------------------------------------- */
body .slds {
    @import 'project';
}

/* ----------------------------------------
Overrides
---------------------------------------- */
body .slds {
    @import 'overrides/slds';
}

@import 'overrides/rich-text',
    'overrides/pdf-viewer';