.conga {
    &-notification {
        min-width: 0;
        width: 100%;
        display: block;
        .slds-list {
            &--dotted {
                margin-left: 1rem;
                margin-top: 0.25rem;
            }
        }
    }
    &-hide {
        display: none !important;
        &--tablet {
            @media (max-width: 48em) {
                display: none !important;
            }
        }
        &--phone {
            @media (max-width: 30em) {
                display: none !important;
            }
        }
    }
    &-show {
        display: block !important;
        @include show-modifiers;
        @media (max-width: 64em) {
            &--desktop {
                display: block !important;
                @include show-modifiers;
            }
        }
        @media (max-width: 48em) {
            &--tablet {
                display: block !important;
                @include show-modifiers;
            }
        }
        @media (max-width: 30em) {
            &--phone {
                display: block !important;
                @include show-modifiers;
            }
        }
    }
    &-button {
        &--responsive {
            @media (max-width: 48em) {
                font-size: 0.6rem;
                line-height: 1.35rem;
                padding: 0 0.5rem;
            }
        }
    }
    &-white-space {
        &--normal {
            white-space: normal;
        }
        &--nowrap {
            white-space: nowrap;
        }
    }
    &-cursor {
        &--pointer {
            cursor: pointer;
        }
        &--default {
            cursor: default;
        }
        &--not-allowed {
            cursor: not-allowed;
        }
    }
    &-pointer-events {
        &--none {
            pointer-events: none;
        }
        &--all {
            pointer-events: all;
        }
    }
    &-opacity {
        &--25 {
            opacity: 0.25;
        }
        &--50 {
            opacity: 0.5;
        }
        &--75 {
            opacity: 0.75;
        }
    }
    &-overflow {
        &--hidden {
            overflow: hidden;
        }
        &--scroll {
            overflow: scroll;
        }
        &--visible {
            overflow: visible;
        }
    }
    &-position {
        &--static {
            position: static;
        }
        &--relative {
            position: relative;
        }
        &--absolute {
            position: absolute;
        }
        &--fixed {
            position: fixed;
        }
    }
    &-vertical-align {
        &--top {
            vertical-align: top;
        }
        &--0 {
            vertical-align: 0;
        }
        &--middle {
            vertical-align: middle;
        }
        &--bottom {
            vertical-align: bottom;
        }
    }
    &-flex-basis {
        &--0 {
            flex-basis: 0;
        }
        &--1 {
            flex-basis: 1;
        }
    }
    &-word-break {
        word-break: break-word;
    }
    &-absolute-center {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
    }
    &-animated {
        transition: all 50ms ease;
        @media (max-width: 64em) {
            position: static !important;
        }
    }
    &-contract-icon {
        position: relative;
        &__check-mark {
            display: none;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translateX(-50%) translateY(-50%);
        }
        &__badge {
            font-size: 8px;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
        }
        &--is-active {
            .conga-contract-icon__document {
                opacity: 0.25;
            }
            .conga-contract-icon__check-mark {
                display: inline-block;
                opacity: 1;
            }
        }
    }
    &-icon-text {
        &-success {
            fill: $color-background-success-dark;
        }
    }
    &-redlines {
        &-iframe {
            iframe {
                display: block;
                width: 100%;
                height: calc(100% - 54px);
                position: absolute;
                top: 54px;
                left: 0;
                border: none;
            }
            &--classic {
                iframe {
                    display: block;
                    position: absolute;
                    width: 100%;
                    height: calc(100% - 73px);
                    top: 73px;
                    left: 0;
                    border: none;
                    @media (max-width: 48em) {
                        height: calc(100% - 65px);
                        top: 65px;
                    }
                }
            }
        }
        &-toolbar {
            background: #061c3f;
            height: 55px;
            position: absolute;
            top: 0;
            color: white;
            width: 100%;
            border-bottom: 1px solid rgba(0, 0, 0, .5);
            &__control {
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                background-color: rgba(white, 0.1);
                padding: 0 $spacing-small;
                margin-top: 14px;
                border-radius: $border-radius-small;
                @media only screen and (max-width: 1024px) {
                    display: none;
                }
            }
            &__title {
                max-width: calc((100vw / 2) - 300px);
                @media (max-width: 64em) {
                    max-width: calc(100vw - 180px);
                }
            }
            select {
                font-size: 12px;
                color: #f2f2f2;
                margin: 0;
                padding: 3px 2px 2px;
                border: none;
                background: transparent;
                display: inline-block;
            }
            .dropdownToolbarButton {
                padding: 0 18px 0 8px;
                margin: 3px 2px 4px 0;
                max-width: none;
                min-width: 16px;
                font-size: 12px;
                line-height: 14px;
                width: auto;
                overflow: hidden;
                border: 1px solid rgba(0, 0, 0, 0.5);
                height: 24px;
                background-color: rgba(white, 0.05);
                &_container {
                    position: relative;
                    display: inline-block;
                    &:before {
                        content: '';
                        display: inline-block;
                        position: absolute;
                        right: 10px;
                        top: 11px;
                        width: 0;
                        height: 0;
                        border-style: solid;
                        border-width: 0 2px 3px 2px;
                        border-color: transparent transparent #ffffff transparent;
                    }
                    &:after {
                        content: '';
                        display: inline-block;
                        position: absolute;
                        right: 10px;
                        bottom: 12px;
                        width: 0;
                        height: 0;
                        border-style: solid;
                        border-width: 3px 2px 0 2px;
                        border-color: #ffffff transparent transparent transparent;
                    }
                }
            }
        }
        &-button {
            display: inline-block;
            min-width: 16px;
            border: 1px solid rgba(0, 0, 0, .65);
            border-radius: 2px;
            color: hsla(0, 0%, 100%, .8);
            font-size: 12px;
            line-height: 14px;
            font-weight: normal;
            cursor: pointer;
            -webkit-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            cursor: default;
            transition-property: background-color, border-color, box-shadow;
            transition-duration: .15s;
            transition-timing-function: ease;
            background-color: hsla(0, 0%, 100%, .09);
            background-image: linear-gradient(hsla(0, 0%, 100%, .05), hsla(0, 0%, 100%, 0));
            padding: 4px 8px;
            top: -1px;
            position: relative;
            &:hover {
                text-decoration: none;
                background-color: hsla(0, 0%, 100%, .11);
                border-color: rgba(0, 0, 0, .4) rgba(0, 0, 0, .43) rgba(0, 0, 0, .45);
            }
        }
        &-subhead {
            position: relative;
            top: -1px;
        }
    }
    &-tree {
        &__item {
            margin-bottom: $spacing-x-small;
            padding-top: $spacing-xxx-small;
            a,
            a:visited {
                color: $color-text-link;
            }
        }
    }
    &-multi-picklist-pills {
        .#{$css-prefix}pill {
            display: inline-flex;
            margin-bottom: $spacing-xx-small;
        }
    }
    &-attachments {
        .#{$css-prefix}item {
            padding: {
                left: 0;
                right: 0;
            }
            &:first-child {
                border-top: none;
                padding-top: 0;
            }
        }
    }
    &-email {
        &-preview {
            border: none;
            width: 100%;
            min-height: 60vh;
            &-spinner {
                position: relative;
                height: 300px;
                background: #f0f1f2;
                .#{$css-prefix}spinner {
                    &_container {
                        background: transparent;
                    }
                }
            }
        }
        &-info {
            max-height: 65vh;
            @media (max-width: 64em) {
                max-height: none;
            }
        }
    }
    &-contract-activity {
        position: relative;
        z-index: 1;
        &__title {
            position: relative;
            .#{$css-prefix} {
                &tile {
                    &__title {
                        background: white;
                        padding-right: 8px;
                    }
                }
                &icon {
                    width: 0.75rem;
                    height: 0.75rem;
                    margin-left: 0.25rem;
                }
            }
        }
    }
    &-timeline {
        &__media {
            &--uploaded {
                &:before {
                    background: #cc6b85;
                }
            }
            &--sent {
                &:before {
                    background: #95aec5;
                }
            }
            &--replied {
                &:before {
                    background: #6b9ee2;
                }
            }
            &--activated {
                &:before {
                    background: #4bc076;
                }
            }
            &--status-changed {
                &:before {
                    background: #f59f71;
                }
            }
            &--deleted {
                &:before {
                    background: #d2bfc4;
                }
            }
            &--switched {
                &:before {
                    background: #8987d0;
                }
            }
        }
        &__icon {
            &--uploaded {
                background: #cc6b85;
            }
            &--sent {
                background: #95aec5;
            }
            &--replied {
                background: #6b9ee2;
            }
            &--activated {
                background: #4bc076;
            }
            &--status-changed {
                background: #f59f71;
            }
            &--deleted {
                background: #d2bfc4;
            }
            &--switched {
                background: #8987d0;
            }
        }
    }
    &-page-control {
        transition: all 500ms ease;
        &--show {
            opacity: 1;
        }
        &--hide {
            opacity: 0;
            @media (max-width: 48em) {
                opacity: 1;
            }
        }
    }
    &-setup {
        &-logo {
            max-width: 320px;
            margin-bottom: $spacing-large;
        }
        &-container {
            /*background: #F4F6F9;*/
        }
        &-content {
            padding: 120px 0;
            width: 100%;
            max-width: 360px;
            z-index: -1;
            margin: 0 auto;
        }
        &-body {
            background: #F4F6F9;
            box-sizing: border-box;
            min-height: 480px;
            height: 100%;
            position: relative;
        }
    }
    &-pill {
        &_container {
            .#{$css-prefix}pill {
                margin-top: $spacing-xx-small;
                margin-right: 0;
                &:hover {
                    background: white;
                }
                + .#{$css-prefix}pill {
                    margin-left: 0;
                }
            }
        }
    }
    &-inline-spinner {
        display: inline-block;
        position: static;
        background-color: transparent;
        margin-left: 0.7rem;
        margin-right: 0.5rem;
        margin-top: 10px;
        margin-bottom: 3px;
        .#{$css-prefix}spinner {
            display: inline-block;
            position: static;
            width: 1rem;
            vertical-align: 0.25rem;
        }
    }
    &-textarea {
        &--tall {
            height: 140px;
        }
    }
    /*&-lookup {
        
    }*/
    &-iframe {
        width: 100%;
        border: 1px solid $color-border;
        border-radius: $border-radius-small;
    }
    &-dimension {
        &-listener {
            position: fixed;
            z-index: 9999;
            right: 0;
            bottom: 0;
        }
        &-controller {
            width: 100%;
            height: 100%;
            position: absolute;
            z-index: 9999;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
        }
    }
    &-overlay {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: transparent;
    }
    &-view-redlines-vf {
        border: none;
        background: transparent;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        height: 100%;
        width: 100%;
    }
    &-close-button {
        cursor: pointer;
        z-index: 99999;
        position: fixed;
        left: calc(100% - 28px);
        top: 72px;
    }
    &-filename-flex {
        width: 0;
    }
    &-z-index {
        &--max {
            z-index: 9999;
        }
        &--9999 {
            z-index: 9999;
        }
        &--0 {
            z-index: 0;
        }
        &---1 {
            z-index: -1;
        }
    }
    &-changed {
        background-color: $color-background-highlight;
        &--transparent {
            background-color: rgba($color-background-highlight, 0.35);
        }
    }
    &-list {
        &--horizontal {
            @media (max-width: 48em) {
                display: block;
                list-style: none;
                .#{$css-prefix}item {
                    &:after {
                        display: none;
                    }
                    &:before {
                        width: 2px;
                        height: 2px;
                        content: '';
                        margin-left: 0;
                        margin-right: 0.5rem;
                        border-radius: 50%;
                        background-color: #54698d;
                    }
                    .#{$css-prefix}badge {
                        line-height: 2;
                    }
                }
            }
        }
    }
    &-compare-selector {
        @media (max-width: 64em) {
            display: none;
        }
    }
}
