.#{$css-prefix} {
    &pill {
        &.#{$css-prefix} {
            &has-error {
                background-color: #fff;
                border-color: #c23934;
                box-shadow: #c23934 0 0 0 1px inset;
                background-clip: padding-box;
            }
        }
    }
    &media {
        &--timeline {
            .#{$css-prefix} {
                &tile {
                    &__title {
                        &:after {
                            position: absolute;
                            right: 0;
                            bottom: .5rem;
                            left: 0;
                            border-bottom: 1px dashed rgb(216, 221, 230);
                            content: '';
                            z-index: -1;
                        }
                    }
                }
            }
        }
    }
    &lookup {
        &__menu {
            @media (max-width: 30em) {
                box-shadow: $shadow-drop-down;
            }
        }
        &__list {
            @media (max-width: 30em) {
                max-height: 110px;
            }
        }
    }
    &breadcrumb {
        @media (max-width: 48em) {
            display: none;
        }
    }
}
